@use "base/variables" as v;
@use "base/mixins" as m;

.header {
  background-color: v.$green;

  @include m.tablet {
    &.fijo {
      left: 0;
      top: 0;
      position: fixed;
      width: 100%;
      box-shadow: 0.1rem 0.2rem 0.3rem #333;
    }
  }

  h1 {
    color: v.$white;
    text-align: center;
    @include m.tablet {
      margin: 0;
    }
  }

  .container-header {
    padding: 2rem;
    @include m.tablet {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  }
}

.main-navigation {
  @include m.tablet {
    display: flex;
    gap: 2rem;
  }
  a {
    display: block;
    text-align: center;
    color: v.$white;
    font-size: 2.4rem;
    margin-bottom: 1rem;
    @include m.tablet {
      margin: 0;
    }

    &:hover,
    &.active {
      color: v.$yellow;
    }
  }
}
