@use "base/variables" as v;
@use "base/mixins" as m;

.tickets {
  padding: 5rem 0;
  h3 {
    text-align: center;
    color: v.$white;
    margin-bottom: 2rem;
  }

  .container-tickets {
    @include m.tablet {
      @include m.grid(2, 5rem);
    }
  }
}

.pase {
  color: black;

  .name-pase {
    text-align: center;
    font-size: 2.8rem;
    color: v.$white;
  }

  &-header,
  &-footer {
    padding: 0.5rem;
    p {
      text-align: center;
      color: v.$white;
      margin: 0;
    }
  }
  &-header {
    background-color: v.$pink;
    p {
      text-transform: uppercase;
      font-size: 1.6rem;
    }
  }

  &-body {
    background-color: v.$white;
    ul {
      @include m.reset-list;
      li,
      :nth-child(odd) {
        background-color: v.$white;
        padding: 1rem;
        border-bottom: 1px solid #e1e1e1;
      }
    }
  }

  &-footer {
    background-color: v.$purple;
    p {
      font-size: 2.4rem;
      font-weight: 700;
    }
  }
}
